.form {
  font-family: "ptsans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
}

@media (min-width: $tablet-width) {
  .form__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: $desktop-width) {
  .form__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.visually-hidden {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
